import { useState, useEffect } from "react";
import ReactDOM from "react-dom/client";
import React from "react";
import {List, ListItem, ListItemText} from "@material-ui/core";
import {ListItemButton} from "@mui/material";

const Test = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    fetch("/api/test?format=json")
      .then((res) => res.json())
      .then((data) => setData(data));
 }, []);

  return (
    <>
      {data &&
        data.map((item) => {
          return (
            <div>
                <h2 key={item.id}>{item.otazka} {item.orig_topic}</h2>
                <List>
                  <ListItem disablePadding>
                    <ListItemButton key="a">
                      <ListItemText primary={item.odpoved_a} />
                    </ListItemButton>
                  </ListItem>
                  <ListItem disablePadding>
                    <ListItemButton key="b">
                      <ListItemText primary={item.odpoved_b} />
                    </ListItemButton>
                  </ListItem>
                  <ListItem disablePadding>
                    <ListItemButton key="c">
                      <ListItemText primary={item.odpoved_c} />
                    </ListItemButton>
                  </ListItem>
                </List>
            </div>
          );
        })}
    </>
  );
};

export default Test;